.footer {
  width: 100%;
  overflow: hidden;
  padding: 6rem 0;
  background-color: rgba(19, 19, 19, 0.8);
}
.footer-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
}

.left {
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.location {
  display: flex;
  margin-bottom: 0.8rem;
}

.location p {
  line-height: 30px;
}
.right {
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.social {
  margin-top: 1rem;
  padding: 0.5rem 0rem;
  display: flex;
  gap:25px;
  text-align: center;
  flex-wrap: wrap;
   width: 300px;
}

h4 {
  font-size: 1.4rem;
  padding-bottom: 0.7rem;
}
p {
  font-size: 1.2rem;
}

@media screen and (max-width: 640px) {
  .footer-container {
    grid-template-columns: 1fr;
  }
  h4 {
    font-size: 1rem;
    padding-bottom: 0.5rem;
  }
}

@media screen and (max-width:300px) {
  .social {
    width: 150px;
    flex-wrap: wrap;
  }
  h4 {
    font-size: 1rem;
    padding-bottom: 0.5rem;
  }

}