.project-heading {
  text-align: center;
  padding: 4rem 0 2rem 0;
}
.project-container {
  max-width: 1140px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
  padding-bottom: 5rem;
  margin: auto;
}
.project-card {
  background: #1a1919;
  padding: 1.2rem 1rem;
  width: 90%;
  margin: auto;
}

.project-card:hover {
  background: #1a1919;
  padding: 1.2rem 1rem;
  width: 100%;
  margin: auto;
 transition: all 3s;
}

.project-card img {
  width: 100%;
}
.project-title {
  color: #fff;
  padding: 1rem 0;
}
.pro-details p {
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}
.pro-btns {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.pro-btns .btn {
  padding: 0.5rem 1rem;
}
@media screen and (max-width: 767px) {
  .project-container {
    max-width: 100%;
    grid-template-columns: 1fr;
  }
  .project-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
    width: 100%;
  }
}
