.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  height: 90px;
  z-index: 1000;
}
.header h1:hover {
  color: #52b788;
}
.header-bg {
  background-color: rgba(0, 0, 0, 0.85);
  transition: 0.5s;
}
.nav-menu {
  display: flex;
}
.nav-menu > li {
  padding: 0 1rem;
}
.nav-menu > li > a {
  font-size: 1.2rem;
  font-weight: 500;
}

.hamburger {
  display: none;
}

.dwn {
  padding: 0.2rem;
  background: linear-gradient(#00ff87, #60efff);
  color: black;
}

@media screen and (max-width: 767px) {
  .nav-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.3s;
  }
  .nav-menu.active {
    left: 0;
    color: red;
  }
  .nav-menu > li {
    padding: 1rem 0;
  }
  .nav-menu > li > a {
    font-size: 2rem;
  }
  .hamburger {
    display: initial;
    margin-left: 200px;
  }
}


